.blog-wrapper {

    .card {
        margin: 30px;
        padding: 10px;
        background-color: rgb(49, 49, 49);
        border: 2px solid rgb(80, 80, 80);
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

        .b-media-wrapper {
            overflow: hidden;
            position: relative;
            max-height: 250px;
        }
    
        .b-media {
            width: 100%;
            height: auto;
            object-fit: contain;
        }
        
        .b-tag {
            display: inline-block;
            background-color: rgb(185, 245, 167);
            color: black;
            transition: background-color 0.3s, color 0.3s;
            text-decoration: none;
        }
    
        .b-tag:hover {
            color: white;
            background-color: rgb(114, 98, 255);
        }
        
        .b-excerpt {
            background-color: rgb(27, 27, 27);

            .b-excerpt-content {
                border: 2px solid rgb(80, 80, 80);
                padding-left: 5px;
                padding-right: 5px;
            }
        }

        @media only screen and (max-width: 576px) {
            .b-media-wrapper {
                max-height: 120px;
            }
        }
    }
}
