.paginate {
    display: flex;
    justify-content: space-between;

    .p-back, .p-next {
        margin-top: 50px;
        display: inline-block;
    }

    .p-back a, .p-next a {
        font-weight: bold;
        color: rgb(255, 255, 255);
        background-color: rgb(16, 50, 94);
        padding: 5px;
    }
}
